.kernel-grid {
  display: grid;
  grid-template-columns: repeat(3, 48px);
  gap: 4px;
  margin: 12px 0;
  justify-content: center;
}

.kernel-grid input[type="number"] {
  width: 48px;
  padding: 7px 6px;
  border: 1px solid #ced4da;
  border-radius: 5px;
  background: #f8f8f8;
  color: #333;
  font-size: 1em;
  text-align: center;
  font-family: "Inter", monospace;
  transition: border-color 0.2s;
}

.kernel-grid input[type="number"]:focus {
  border-color: #007bff;
  outline: none;
}

.gradation-dialog label {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  margin-right: 18px;
  color: #555;
  font-weight: 600;
  font-size: 0.97em;
}

.gradation-dialog select {
  padding: 7px 18px 7px 10px;
  border: 1px solid #ced4da;
  border-radius: 5px;
  background-color: #fff;
  color: #333;
  font-size: 1em;
  font-family: "Inter", sans-serif;
  margin-left: 8px;
  cursor: pointer;
  appearance: none;
  background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23333333%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-6.5%200-12.1%204.6-14%2011.2a17.6%2017.6%200%200%200%202.3%2013l128%20127.9c3.9%203.9%208.8%205.8%2013.8%205.8s9.9-1.9%2013.8-5.8L285%2082.6a17.6%2017.6%200%200%0A0%202-13.2z%22%2F%3E%3C%2Fsvg%3E");
  background-repeat: no-repeat;
  background-position: right 10px center;
  background-size: 10px auto;
}

.gradation-dialog input[type="checkbox"] {
  accent-color: #007bff;
  margin-right: 6px;
}

.gradation-dialog button {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 18px;
  font-size: 0.97em;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s, box-shadow 0.2s;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.08);
}

.gradation-dialog button:hover:not(:disabled) {
  background-color: #0056b3;
}

.gradation-dialog button:active:not(:disabled) {
  background-color: #004085;
}

.gradation-dialog button:disabled {
  background-color: #cccccc;
  color: #999999;
  cursor: not-allowed;
  box-shadow: none;
}

.gradation-dialog > div {
  display: flex;
  flex-wrap: wrap;
  gap: 10px 18px;
  align-items: center;
}

@media (max-width: 576px) {
  .gradation-dialog {
    width: 98vw;
    padding: 10px 4px;
    font-size: 0.97em;
    min-width: 0;
  }
  .kernel-grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 2px;
  }
  .kernel-grid input[type="number"] {
    width: 100%;
    font-size: 0.93em;
    padding: 6px 2px;
  }
  .gradation-dialog label {
    margin-right: 8px;
    font-size: 0.93em;
  }
  .gradation-dialog select {
    font-size: 0.93em;
    padding: 6px 10px 6px 8px;
  }
  .gradation-dialog button {
    padding: 8px 0;
    font-size: 0.93em;
  }
}
