/* gradation-dialog (градационная коррекция) — стили в стиле index.css */

.gradation-dialog {
  position: absolute;
  width: 420px;
  max-width: 98vw;
  background: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  box-shadow: 0 4px 18px rgba(0, 0, 0, 0.1);
  padding: 28px 24px 22px 24px;
  color: #333;
  font-size: 1em;
  font-family: "Inter", sans-serif;
  margin: 0 auto;
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.gradation-dialog h2 {
  color: #34495e;
  font-size: 1.3em;
  margin-top: 0;
  margin-bottom: 18px;
  border-bottom: 1px solid #e0e0e0;
  padding-bottom: 8px;
  text-align: center;
  font-weight: 700;
  letter-spacing: 0.5px;
}

.gradation-dialog svg {
  display: block;
  margin: 0 auto 10px auto;
  background: #181818;
  border-radius: 7px;
  border: 1px solid #e0e0e0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.07);
  width: 100%;
  max-width: 350px;
  height: 100px;
}

.gradation-dialog label {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  margin-right: 18px;
  color: #555;
  font-weight: 600;
  font-size: 0.97em;
}

.gradation-dialog input[type="number"] {
  width: 60px;
  padding: 6px 8px;
  border: 1px solid #ced4da;
  border-radius: 5px;
  font-size: 1em;
  background: #f8f8f8;
  color: #333;
  margin-left: 4px;
  margin-right: 8px;
  font-family: "Inter", sans-serif;
  transition: border-color 0.2s;
}

.gradation-dialog input[type="number"]:focus {
  border-color: #007bff;
  outline: none;
}

.gradation-dialog input[type="checkbox"] {
  accent-color: #007bff;
  margin-right: 6px;
}

.gradation-dialog button {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 18px;
  font-size: 0.97em;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s, box-shadow 0.2s;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.08);
}

.gradation-dialog button:hover:not(:disabled) {
  background-color: #0056b3;
}

.gradation-dialog button:active:not(:disabled) {
  background-color: #004085;
}

.gradation-dialog button:disabled {
  background-color: #cccccc;
  color: #999999;
  cursor: not-allowed;
  box-shadow: none;
}

.gradation-dialog button:last-child {
  background: #ff00ff;
  color: #fff;
  font-weight: 700;
  margin-left: auto;
  width: 100%;
  margin-top: 0;
  margin-bottom: 0;
  border-radius: 5px;
  box-shadow: 0 2px 8px rgba(255, 0, 255, 0.08);
  transition: background 0.2s;
}

.gradation-dialog button:last-child:hover:not(:disabled) {
  background: #c800c8;
}

.gradation-dialog > div {
  display: flex;
  flex-wrap: wrap;
  gap: 10px 18px;
  align-items: center;
}

@media (max-width: 576px) {
  .gradation-dialog {
    width: 98vw;
    padding: 10px 4px;
    font-size: 0.97em;
    min-width: 0;
  }
  .gradation-dialog svg {
    max-width: 98vw;
    height: 80px;
  }
  .gradation-dialog label {
    margin-right: 8px;
    font-size: 0.93em;
  }
  .gradation-dialog input[type="number"] {
    width: 48px;
    font-size: 0.93em;
  }
  .gradation-dialog button {
    padding: 8px 0;
    font-size: 0.93em;
  }
}
